/* Game.css */

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background: linear-gradient(135deg, #ff9a9e 0%, #fad0c4 100%);
  color: white;
  animation: gradientAnimation 15s ease infinite;
}

@keyframes gradientAnimation {
  0% { background: linear-gradient(135deg, #ff9a9e 0%, #fad0c4 100%); }
  50% { background: linear-gradient(135deg, #a18cd1 0%, #fbc2eb 100%); }
  100% { background: linear-gradient(135deg, #ff9a9e 0%, #fad0c4 100%); }
}

.game-container {
  text-align: center;
}

.title {
  font-size: 36px;
  margin-bottom: 20px;
  color: #fff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.game {
  display: flex;
  flex-direction: row;
  background: rgba(0, 0, 0, 0.8);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  animation: popIn 0.5s ease;
}

@keyframes popIn {
  0% { transform: scale(0); opacity: 0; }
  100% { transform: scale(1); opacity: 1; }
}

.game-board {
  margin-right: 20px;
}

.status {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
  color: #fad0c4;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.board-row {
  display: flex;
}

.square {
  background: #444;
  border: 2px solid #555;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 50px;
  height: 50px;
  margin-right: -2px;
  margin-top: -2px;
  padding: 0;
  text-align: center;
  width: 50px;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  color: #ff9a9e;
  border-radius: 10px;
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  0% { opacity: 0; transform: scale(0.8); }
  100% { opacity: 1; transform: scale(1); }
}

.square:hover {
  background: #555;
  transform: scale(1.1);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.square:focus {
  outline: none;
  background: #fad0c4;
  color: #282c34;
}

.game-info {
  margin-left: 20px;
}

.game-info ol, .game-info ul {
  padding-left: 30px;
  max-height: 200px;
  overflow-y: auto;
  background: rgba(255, 255, 255, 0.1);
  padding: 10px;
  border-radius: 10px;
}

.game-info button {
  background: #ff9a9e;
  border: none;
  padding: 10px 15px;
  margin-bottom: 5px;
  cursor: pointer;
  color: #282c34;
  font-size: 16px;
  font-weight: bold;
  transition: background 0.3s ease, color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  border-radius: 10px;
}

.game-info button:hover {
  background: #282c34;
  color: #ff9a9e;
  transform: scale(1.05);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.game-info button:focus {
  outline: none;
}
